import "./filterSection.css";
import React, { useState, useCallback } from "react";
import PropTypes from "prop-types";
import SearchButton from "../SearchButton/SearchButton";
import { AUTHORS, CATEGORIES, SOURCES } from "../../utils/constants";

export const FilterCheckbox = ({ label, handlePersonalizedFeedChange }) => (
  <label className="filter-checkbox">
    <input
      type="checkbox"
      name={label.toLowerCase()}
      onChange={handlePersonalizedFeedChange}
    />
    {label}
  </label>
);

export const FilterBlock = ({ title, data, setData }) => {
  const handleDataChange = useCallback(
    (event) => {
      const { name, checked } = event.target;
      setData((prevSelected) =>
        checked
          ? [...prevSelected, name]
          : prevSelected.filter((item) => item !== name)
      );
    },
    [setData]
  );

  return (
    <div className="filter-block">
      <h3>{title}</h3>
      {data.map((item, index) => (
        <FilterCheckbox
          key={index}
          label={item}
          handlePersonalizedFeedChange={handleDataChange}
        />
      ))}
    </div>
  );
};

const FilterSection = ({ handlePersonalizedNewsFeed }) => {
  const [selectedSources, setSelectedSources] = useState([]);
  const [selectedCategories, setSelectedCategories] = useState([]);
  const [selectedAuthors, setSelectedAuthors] = useState([]);

  const handlePersonalizedFeedClick = useCallback(() => {
    const filters = {
      sources: selectedSources,
      categories: selectedCategories,
      authors: selectedAuthors,
    };
    handlePersonalizedNewsFeed(filters);
  }, [
    selectedSources,
    selectedCategories,
    selectedAuthors,
    handlePersonalizedNewsFeed,
  ]);

  return (
    <aside className="personalized-feed">
      <h2>Personalized News Feed</h2>
      <p>Select your preferred sources, categories, and authors:</p>
      <div className="filter-options">
        <FilterBlock
          title="Sources"
          data={SOURCES}
          setData={setSelectedSources}
        />
        <FilterBlock
          title="Categories"
          data={CATEGORIES}
          setData={setSelectedCategories}
        />
        <FilterBlock
          title="Authors"
          data={AUTHORS}
          setData={setSelectedAuthors}
        />
      </div>
      <SearchButton
        className="search-button"
        handleSearch={handlePersonalizedFeedClick}
        value="Personalized Feed"
      />
    </aside>
  );
};

FilterSection.propTypes = {
  handlePersonalizedNewsFeed: PropTypes.func.isRequired,
};

export default FilterSection;
